import { EventTouch, _decorator } from 'cc';
import BaseComponent from '../../../../Model/ComponentBase';
import PublicModel from '../../../../Model/PublicModel';
const { ccclass, property } = _decorator;
@ccclass('LinkItem')
export default class LinkItem extends BaseComponent {
    type: URLType
    init() {
        this.type = PublicModel.getInstance.getEnumValueFromString(this.node.name.split("Link")[0], URLType)

    }
    onOpenWindow(e: EventTouch, customEventData?: string) {
        window.open(this.type)
    }
}

enum URLType {
    /**官方 */
    Official = "https://godplay.app/",
    FaceBook = "https://www.facebook.com/GodPlayGameFun/",
    Instagram = "https://www.instagram.com/explore/tags/gpg/",
}